<template>
  <div class="orderList">
    <div class="head flex-between">
      <div class="nav">
        <span
          :class="[{active:curIndex === index}]"
          v-for="(item,index) in tab"
          :key="index"
          @click="change(item,index)"
        >{{item.title}}</span>
      </div>

      <div class="product" v-for="item in tabList" :key="item.order_id">
        <router-link :to="{name:'orderDetail',query:{id:item.order_id}}">
          <div class="info_top">
            <span class="number">订单号：{{item.order_sn}}</span>
            <span class="priceState">{{item.order_status_detail}}</span>
          </div>
          <div class="info_center">
            <img :src="item.order_goods[0].original_img" />
            <div class="center_right">
              <p class="title">{{item.order_goods[0].goods_name}}</p>
              <p
                class="price"
              >￥{{item.order_goods[0].goods_price}} x{{item.order_goods[0].goods_num}}</p>
            </div>
          </div>
          <p class="allPrice">
            总计:
            <span class="red">￥{{item.total_amount}}</span>
          </p>
          <div class="info_bottom">
            <span class="contact">联系客服</span>
            <span class="cancel">取消订单</span>
            <span class="pay">付款</span>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import url from "@/axios/api.js";
export default {
  data() {
    return {
      curIndex: 0,
      tabList: [],
      type: "",
      tab: [
        {
          title: "所有订单",
          loading: false,
          page: 1,
          finished: false,
          list: [],
          type: "all"
        },
        {
          title: "待付款",
          loading: false,
          page: 1,
          finished: false,
          list: [],
          type: 1
        },
        {
          title: "待发货",
          loading: false,
          page: 1,
          finished: false,
          list: [],
          type: 2
        },
        {
          title: "配送中",
          loading: false,
          page: 1,
          finished: false,
          list: [],
          type: 3
        },
        {
          title: "已完成",
          loading: false,
          page: 1,
          finished: false,
          list: [],
          type: 4
        }
      ]
    };
  },
  created() {
    this.getData();
  },
  methods: {
    change(item, index) {
      this.curIndex = index;
      if (index === 0) {
        this.type = "";
        this.getData();
      } else if (index === 1) {
        this.type = "WAITPAY";
        this.getData();
      } else if (index === 2) {
        this.type = "WAITSEND";
        this.getData();
      } else if (index === 3) {
        this.type = "WAITRECEIVE";
        this.getData();
      } else if (index === 4) {
        this.type = "FINISH";
        this.getData();
      }
    },
    getData() {
      let params = new URLSearchParams();
      params.append("openid", localStorage.getItem("openid"));
      params.append("type", this.type);
      params.append("p", 1);
      axios.post(url.orderList, params).then(res => {
        this.tabList = res.data.data;
      });
    }
  }
};
</script>

<style scoped lang="scss">
a {
  text-decoration: none;
}
.active {
  color: #ff4c23 !important;
  border-bottom: 1px solid #ff4c23;
}
.nav {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background: #fff;
  span {
    font-size: 13px;
    font-family: SourceHanSansCN-Regular;
    font-weight: 400;
    color: rgba(107, 107, 107, 1);
    line-height: 28px;
  }
}

.product {
  margin-top: 18px;
  padding: 18px;
  background: #fff;
  .info_top {
    display: flex;
    padding: 10px;
    span {
      font-size: 14px;
      font-family: SourceHanSansCN-Regular;
      font-weight: 400;
      color: rgba(0, 0, 0, 1);
      line-height: 20px;
    }
    .priceState {
      margin-left: auto;
      font-size: 14px;
      font-family: SourceHanSansCN-Regular;
      font-weight: 400;
      color: rgba(248, 144, 10, 1);
      line-height: 20px;
    }
  }
  .info_center {
    height: 85px;
    display: flex;
    background-color: rgb(250, 250, 250);
    padding-top: 10px;
    img {
      height: 60px;
      width: 60px;
    }
    .center_right {
      margin-left: 10px;
      .title {
        font-size: 14px;
        font-family: SourceHanSansCN-Medium;
        font-weight: 500;
        color: rgba(69, 69, 69, 1);
        line-height: 13px;
      }
      .price {
        font-size: 14px;
        font-family: SourceHanSansCN-Regular;
        font-weight: 400;
        color: rgba(69, 69, 69, 1);
        line-height: 13px;
        margin-top: 30px;
      }
    }
  }
  .allPrice {
    text-align: right;
    color: #000;
    .red {
      color: #ef1818;
    }
  }
  .info_bottom {
    margin-top: 15px;
    display: flex;
    justify-content: center;
    span {
      font-size: 13px;
      border: 1px solid rgba(238, 238, 238, 1);
      border-radius: 14px;
      padding: 5px 15px;
      color: #000;
    }
    .pay {
      padding: 5px 29px;
      background: rgba(255, 76, 35, 1);
      color: #fff;
    }
  }
}
</style>


